<template>
	<div class="jump">
		找不到该网页，错误代码：404
		<router-link to="/luckyit">回到首页</router-link>
	</div>
</template>

<script>
export default {
	name: 'jump',
	data () {
		return {
			
		};
	},
	created() {
		this.jump()
		// this.$router.replace('/luckyit/Goods')
  },
  methods: {
    // 路由参数跳转
    jump(){
			let query = this.$route.query
			if(query.path){
				// console.log('跳转' + '/luckyit/' + query.path);
				this.$router.replace({
          path: '/luckyit/' + query.path,
          query
        })
			}
      // console.log(this.$router);	
    }
  },
}
</script>

